
    import EyeOutline from 'vue-material-design-icons/EyeOutline.vue'
    import EyeOffOutline from 'vue-material-design-icons/EyeOffOutline.vue'

    export default {
        name:"PasswordField",
        components:{
            EyeOutline,
            EyeOffOutline
        },
        props:{
            label: String,
            disabled:{
                type: Boolean,
                default: false,
            },
            modelValue: {
                type: String,
                default: "",
            },
        },
        data(){
            return{
                showPassword: false,
            }
        }
    }
